@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap);
*{box-sizing:border-box;margin:0%;padding:0%}body{margin:0;font-family:-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;display:flex;max-width:1440px;width:100%}html{display:flex;justify-content:center}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}
.App{display:flex;justify-content:center;align-items:center;flex-direction:column;height:auto;width:auto}
.banner{display:flex;width:auto;max-height:480px}.banner-image{display:block;z-index:-1;max-height:480px;max-width:100%}.title{position:relative;display:flex;top:-480px;color:#fff;margin:284px 0 48px 132px;font-family:"Source Sans Pro",sans-serif;font-weight:700;font-size:64px;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}
.header{display:flex;flex-direction:row;justify-content:space-between;max-width:1440px;height:80px;padding:0 42px 0 42px}.user-info{display:flex;flex-direction:row;justify-content:space-between;align-items:center}
.boxuser{display:flex;justify-content:center;align-items:center;font-size:24px;width:auto}
.modalreedem{display:flex;align-items:center;justify-content:center;height:400px;width:600px;background-color:#fff;border-radius:20px;flex-direction:column;margin:0;box-sizing:border-box}.modal-wrapper{display:none;position:fixed;align-items:center;justify-content:center;width:100vh;height:0%;z-index:999}.modal-logo-container{background-color:#46c9a1;display:flex;height:50%;width:100%;align-items:center;justify-content:center;border-radius:20px 20px 0 0}.modal-circle{color:#fff;width:105px;height:105px;display:flex;border-radius:50%;border:5px solid #fff;padding:0;font-size:90px;justify-content:center;align-items:center;font-family:apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji"}.modal-content{display:flex;flex-direction:column;color:#434f65;height:50%;width:100%;padding:30px;justify-content:space-around;align-items:center}.modal-btn{display:flex;width:150px;height:50px;align-items:center;justify-content:center;border-radius:75px;font-size:20px;font-weight:700;color:#fff;background-color:#46c9a1;border:none}.modal-wrapper.is-open{display:flex}
.boxcoin{display:flex;justify-content:center;align-items:center;font-size:24px;height:48px;border-radius:20.5px;background:#ededed;margin-left:20px;padding:0 12px 0 12px;letter-spacing:-0.15px}
.navbar{display:flex;position:absolute;flex-direction:column;top:100px;background-color:#5ccce0a4;border:2px solid #dbdbdb;width:auto;height:auto;padding:12px 12px 12px 12px;overflow:hidden;transition:background 3s;border-radius:20px}.navbar-nav{display:flex;max-width:100%;height:100%}
.navpoints{color:#fff;font-weight:400;font-size:24px;padding:12px 12px 12px 12px}.navpoints:hover{background-color:#17dbff;border:1px solid;border-radius:10px}
.button-points{display:flex;justify-content:center;align-items:center;font-size:24px;height:48px;border-radius:20.5px;background:#ededed;margin:0 0px 0 20px;padding:0 12px 0 12px;letter-spacing:-0.15px;border:none}.button-points:hover{color:#fff;background:#0ad4fa;-webkit-transform:scale(1.2);transform:scale(1.2);-webkit-transform:rotateZ(-10deg);transform:rotateZ(-10deg)}
.linkbutton{display:flex;justify-content:center;align-items:center;font-size:24px;height:48px;border-radius:20.5px;background:#ededed;margin:0 20px 0 20px;padding:0 12px 0 12px;letter-spacing:-0.15px;border:none;text-decoration:none}.linkbutton:hover{color:#fff;background:#0ad4fa;-webkit-transform:scale(1.2);transform:scale(1.2);-webkit-transform:rotateZ(-10deg);transform:rotateZ(-10deg)}
.body{display:flex;flex-direction:column;background-color:#f2f2f2;max-width:1440px;min-height:1000px;padding:0 132px 0 132px}
.pagebutton{display:flex;box-sizing:border-box;justify-content:center;align-items:center;height:48px;width:48px;border-radius:50%;border:1px solid #d9d9d9;color:#d9d9d9;font-size:24px;margin:auto 12px 0 12px}.pagebutton:hover{color:#fff;background:#0ad4fa;-webkit-transform:scale(1.2);transform:scale(1.2)}
.Menu{display:flex;flex-direction:row;justify-content:space-between;align-items:center;width:auto;height:auto;font-size:18px;flex-wrap:wrap;border-bottom:1px solid #d9d9d9;padding:24px 0 24px 0;margin:0 0 42px 0}.text-sortBy{font-family:Source Sans Pro;font-style:normal;font-weight:normal;font-size:18px;line-height:48px;letter-spacing:-0.15px;color:#616161;margin:6px auto 6px 24px;width:auto}.menu-container-sortbuttons{display:flex;width:auto;margin:6px 0 6px 0}.menu-container-arrows{display:flex;width:auto;justify-content:flex-end;align-items:center;align-self:right;margin:12px 0 12px auto}
.quantyproducts{font-family:Source Sans Pro;font-style:normal;font-weight:normal;font-size:18px;line-height:48px;letter-spacing:-0.15px;color:#616161;font-weight:400;padding-right:24px;border-right:1px solid #d9d9d9}
.sortbutton{display:flex;justify-content:center;align-items:center;width:auto;height:auto;background:#dbdbdb;color:#a3a3a3;border:none;border-radius:20.5px;line-height:48px;font-size:18px;letter-spacing:-0.15px;font-weight:400;padding:0px 12px 0px 12px;margin:0px 12px 0px 12px}.sortbutton:hover{color:#fff;background:#0ad4fa;-webkit-transform:scale(1.2);transform:scale(1.2);-webkit-transform:rotateZ(-10deg);transform:rotateZ(-10deg)}
.pagenumber{display:flex;box-sizing:border-box;justify-content:center;align-items:center;height:24px;width:24px;border-radius:50%;border:1px solid #d9d9d9;color:#d9d9d9;font-size:24px;margin:0 3px 0 3px;padding:18px}.pagenumber:hover{color:#fff;background:#0ad4fa;-webkit-transform:scale(1.2);transform:scale(1.2)}
.results{display:flex;flex-direction:row;flex-wrap:wrap;background-color:#f2f2f2;justify-content:space-between}
.resultbox{display:flex;flex-direction:column;justify-content:center;align-items:center;width:276px;height:276px;margin:0 0 24px 0;background-color:#fff}.resultbox-normal{-webkit-transform:translateY(0px);transform:translateY(0px)}.resultbox-hover{-webkit-transform:translateY(-12px);transform:translateY(-12px)}.resultbox-blue{position:absolute;width:276px;height:276px;flex-wrap:wrap;justify-content:center;align-items:center;padding:30% 0 30% 0}.money-quanty{color:#fff;font-size:36px;margin-right:5px}.resultbox-blue-normal{display:none}.resultbox-blue-hover{background:linear-gradient(180deg, rgba(10, 212, 250, 0.86) 0%, rgba(37, 187, 241, 0.86) 100%);display:flex}.button-redeem{display:flex;background-color:#fff;width:229px;height:42px;border-radius:21px;font-size:18px;align-items:center;justify-content:center;border:none}.container-text{display:flex;flex-direction:column;align-items:left;justify-content:center;width:100%;height:100%;padding:0 0 0 24px}.container-img{display:flex;width:252px;height:182px;border-bottom:1px solid #d9d9d9}.category{display:flex;font:16px;color:#616161}.name{display:flex;font:16px;color:#000}.buy-icon-position{display:flex;position:absolute;top:10px;right:10px}.buy-text-position{display:flex;justify-content:center;align-items:center;color:#fff;font-size:14px;position:absolute;top:10px;right:20px;background:#616161;height:42px;width:142px;border-radius:100px;opacity:.8}.margin-left-5px{margin:0 0 0 5px}.visible{display:flex}.invisible{display:none}
.history{display:flex;flex-direction:row;flex-wrap:wrap;background-color:#f2f2f2;justify-content:space-between}
.resultboxhistory{display:flex;flex-direction:row;justify-content:left;align-items:center;width:45%;height:276px;margin:0 0 24px 0;background-color:#fff}.container-text-history{display:flex;flex-direction:column;align-items:left;justify-content:center;width:auto;height:auto;padding:0 0 0 24px}.container-img-history{display:flex;width:252px;height:182px}.category-history{display:flex;font:16px}.name-history{display:flex;font:16px;color:#000}
.modalreedem-error{display:flex;align-items:center;justify-content:center;height:400px;width:600px;background-color:#fff;border-radius:20px;flex-direction:column;margin:0;box-sizing:border-box}.modal-error-wrapper{display:none;position:fixed;align-items:center;justify-content:center;width:100vh;height:0%;z-index:999}.modal-error-logo-container{background-color:#f15e5e;display:flex;height:50%;width:100%;align-items:center;justify-content:center;border-radius:20px 20px 0 0}.modal-error-circle{color:#fff;width:105px;height:105px;display:flex;border-radius:50%;border:5px solid #fff;padding:0;font-size:90px;justify-content:center;align-items:center;font-family:apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji"}.modal-error-content{display:flex;flex-direction:column;color:#434f65;height:50%;width:100%;padding:30px;justify-content:space-around;align-items:center}.modal-error-btn{display:flex;width:150px;height:50px;align-items:center;justify-content:center;border-radius:75px;font-size:20px;font-weight:700;color:#fff;background-color:#f15e5e;border:none}.modal-error-wrapper.is-open{display:flex}
